  height: 100%;
}

.slider__item {
  cursor: pointer;
  transition: 0.3s;
  border: 1px solid transparent;
  width: 100%;
  border-radius: 10px;
}
.slider__item:hover {
  border: 1px solid #fff;
  border-radius: 10px;
}
.slider__item img {
  width: 100%;
  display: block;
  width: 100%;
  border-radius: 10px;
}

@media screen and (max-width: 992px) {
  .user-home__right {
    height: auto;
  }
  .slider__item {
    width: auto;
    height: auto;
  }
